import { ICharCodesMap, IOptions } from './type';
const baseCharCodes: ICharCodesMap = {
  "A": "01",
  "B": "1000",
  "C": "1010",
  "D": "100",
  "E": "0",
  "F": "0010",
  "G": "110",
  "H": "0000",
  "I": "00",
  "J": "0111",
  "K": "101",
  "L": "0100",
  "M": "11",
  "N": "10",
  "O": "111",
  "P": "0110",
  "Q": "1101",
  "R": "010",
  "S": "000",
  "T": "1",
  "U": "001",
  "V": "0001",
  "W": "011",
  "X": "1001",
  "Y": "1011",
  "Z": "1100",
  "0": "11111",
  "1": "01111",
  "2": "00111",
  "3": "00011",
  "4": "00001",
  "5": "00000",
  "6": "10000",
  "7": "11000",
  "8": "11100",
  "9": "11110",
  ".": "010101",
  ",": "110011",
  "?": "001100",
  "'": "011110",
  "!": "101011",
  "/": "10010",
  "(": "10110",
  ")": "101101",
  "&": "01000",
  ":": "111000",
  ";": "101010",
  "=": "10001",
  "+": "01010",
  "-": "100001",
  "_": "001101",
  "\"": "010010",
  "$": "0001001",
  "@": "011010"
};

const getBaseMorseCodes = (options: IOptions) => {
  return {
    ...baseCharCodes,
    [options.separator]: options.space,
  };
};

export const getMorseCodes = (options: IOptions) => {
  const obj: ICharCodesMap = {};
  const baseCharCodesMap = getBaseMorseCodes(options);
  Object.keys(baseCharCodesMap).forEach((key) => {
    obj[key] = baseCharCodesMap[key].replace(/0/g, options.dot).replace(/1/g, options.dash);
  });
  return obj;
};

export const swapMorseCodes = (options: IOptions) => {
  const obj: ICharCodesMap = {};
  const morseCodes = getMorseCodes(options);
  Object.keys(morseCodes).forEach((key) => {
    obj[morseCodes[key]] = key;
  });
  return obj;
};

export const getMorseCodesKey = () => {
  const keyMap: string[] = [];
  Object.keys(baseCharCodes).forEach((key) => {
    keyMap.push(key);
  });
  return keyMap;
};

// 文本中文转Unicode
export const text2unicode = (value: string) => {
  return value.replace(/([\u4E00-\u9FA5]|[\uFE30-\uFFA0])/g, (char) => {
    return `\/u${char.charCodeAt(0).toString(16)}`;
  });
};

// Unicode转文本
export const unicode2text = (values: string[] = []) => {
  return values.map(value => {
    return value.split('\/U').map(code => {
      return isNaN(+parseInt(code, 16)) ? code : String.fromCharCode(+parseInt(code, 16).toString(10));
    }).join('');
  });
};
