import { IOptions } from './type';

const getOptions = (opts: Partial<IOptions> = {}): IOptions => {
  const options: IOptions = {
    ...opts,
    dash: opts.dash || '-',
    dot: opts.dot || '.',
    space: opts.space || '/',
    separator: opts.separator || ' ',
    invalid: opts.invalid || null,
    unit: opts.unit || 0.08,
    fwUnit: opts.fwUnit || opts.unit || 0.08,
    oscillator: {
      ...opts.oscillator,
      type: opts.oscillator?.type || 'sine',
      frequency: opts.oscillator?.frequency || 500,
      onended: opts.oscillator?.onended || null,
    }
  };
  return options;
};

export default getOptions;
