import React, { useEffect, useState } from 'react';
import { Input, Button, Space } from 'antd';
import { CaretRightOutlined, ReloadOutlined, PauseOutlined } from '@ant-design/icons';
import { text2morse, morse2text, morseAudioPlayer } from '@/utils/morse/index';
import { randomDisorderEnText } from '@/utils/morse/extend';
import { speechAudioPlayer } from '@/utils/speech';
import styles from './index.less';

const Page = () => {
  const [text, setText] = useState<string>('');
  const [morse, setMorse] = useState<string>('');
  const [playingMorse, setPlayingMorse] = useState<boolean>(false);
  const [playingText, setPlayingText] = useState<boolean>(false);
  const canMorsePlay = !!window.AudioContext;
  const canSpeechPlay = !!window.speechSynthesis;

  const { play: audioMorsePlay, stop: stopMorsePlay } = morseAudioPlayer({
    oscillator: {
      onended: () => {
        setPlayingMorse(false);
      }
    }
  });

  const { play: audioTextPlay, stop: stopTextPlay } = speechAudioPlayer({
    onend: () => {
      setPlayingText(false);
    }
  });

  // useEffect(() => {
  //   return () => {
  //     if (canSpeechPlay) {
  //       stopTextPlay();
  //     }
  //   };
  // });

  const onUpdatePlayMorseState = () => {
    stopPlayer();
    // todo 格式化数据
    if (!playingMorse) {
      setPlayingMorse(true);
      audioMorsePlay(morse);
    }
  };

  const onUpdatePlayTextState = () => {
    if (!playingText) {
      setPlayingText(true);
      audioTextPlay(text);
    } else {
      stopPlayer();
    }
  };

  const onRandomDisorderEnText = () => {
    const _text = randomDisorderEnText();
    setText(_text);
    setMorse(text2morse(_text));
    stopPlayer();
  };

  const handleTextChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    const _text = e.target.value;
    if (_text !== text) {
      setText(_text);
      setMorse(text2morse(_text));
      stopPlayer();
    }
  };

  const handleMorseChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    const _morse = e.target.value;
    console.log(_morse, morse);
    if (_morse !== morse) {
      setMorse(_morse);
      setText(morse2text(_morse));
      stopPlayer();
    }
  };

  // todo 补充
  const stopPlayer = () => {
    if (playingMorse) {
      setPlayingMorse(false);
      stopMorsePlay();
    }
    if (playingText) {
      setPlayingText(false);
      stopTextPlay();
    }
  };

  return (
    <div className={styles.container}>
      <div className={styles.header}>
        <h1>摩斯密码转换播放器</h1>
        <h2>Morse Code Translater</h2>
      </div>
      <div className={styles.translate}>
        <div className={styles.wrap}>
          <h3>Text</h3>
          <Input.TextArea
            key="textArea-text"
            className={styles.textArea}
            value={text}
            onChange={handleTextChange}
          />
          <div className={styles.btn}>
            <Space size={8}>
              <Button
                size="small"
                icon={<ReloadOutlined />}
                onClick={onRandomDisorderEnText}
                ghost
              >
                生成随机信息
              </Button>
              {canSpeechPlay && (
                <Button
                  type="primary"
                  size="small"
                  icon={playingText ? <PauseOutlined /> : <CaretRightOutlined />}
                  onClick={onUpdatePlayTextState}
                  disabled={!text}
                  ghost
                >
                  {playingText ? '停止' : '播放'}
                </Button>
              )}
            </Space>
          </div>
        </div>
        <div className={styles.wrap}>
          <h3>Morse</h3>
          <Input.TextArea
            key="textArea-morse"
            className={styles.textArea}
            value={morse}
            onChange={handleMorseChange}
          />
          <div className={styles.btn}>
            {canMorsePlay && (
              <Button
                type="primary"
                size="small"
                icon={playingMorse ? <PauseOutlined /> : <CaretRightOutlined />}
                onClick={onUpdatePlayMorseState}
                disabled={!morse}
                ghost
              >
                {playingMorse ? '停止' : '播放'}
              </Button>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Page;
