import { getMorseCodesKey } from './charCodes';

export const randomDisorderEnText = () => {
  const len = Math.floor(Math.random() * 150) + 50;
  const chars = [' '].concat(getMorseCodesKey()).join('');
  let disorderEnText = '';
  for (let index = 0; index < len; index++) {
    const randomNum = Math.floor(Math.random() * chars.length);
    disorderEnText += chars.substring(randomNum, randomNum + 1);
  }
  return disorderEnText;
};
