import { IOptions } from './type';

const audio = (options: IOptions) => {

  let context: AudioContext = new window.AudioContext();

  let source: AudioBufferSourceNode = context.createBufferSource();
  source.connect(context.destination);
  source.onended = options.oscillator.onended;

  const render = (oscillator: any, offlineContext: OfflineAudioContext) => {
    return new Promise<void>(resolve => {
      oscillator.start(0);
      offlineContext.startRendering();
      offlineContext.oncomplete = (e) => {
        source.buffer = e.renderedBuffer;
        resolve();
      };
    });
  };

  let timeout: any;

  const canPlay = () => {
    return !!window.AudioContext;
  };

  const play = async (morse: string) => {
    const [gainValues, totalTime] = getGainTimings(morse, options);
    console.log(gainValues, totalTime);
    const offlineContext: OfflineAudioContext = new window.OfflineAudioContext(1, 44100 * totalTime, 44100);
    const oscillator: any = offlineContext.createOscillator();
    const gainNode = offlineContext.createGain();
    oscillator.type = options.oscillator.type;
    oscillator.frequency.value = options.oscillator.frequency;

    gainValues.forEach(([value, time]: any) => gainNode.gain.setValueAtTime(value, time));

    oscillator.connect(gainNode);
    gainNode.connect(offlineContext.destination);
    await render(oscillator, offlineContext);
    source.start(context.currentTime);
    timeout = setTimeout(() => stop(), totalTime * 1000);
  };

  const stop = () => {
    console.log('stop');
    clearTimeout(timeout);
    timeout = 0;
    source.stop(0);
  };

  return {
    canPlay,
    play,
    stop,
  };
};

const getGainTimings = (morse: string, opts: IOptions): [[[number, number]?], number] => {
  const currentTime = 0;
  const timings: [[number, number]?] = [];
  let time: number = 0;

  timings.push([0, time]);

  const tone = (i: number) => {
    timings.push([1, currentTime + time]);
    time += i * opts.unit;
  };

  const silence = (i: number) => {
    timings.push([0, currentTime + time]);
    time += i * opts.unit;
  };

  const gap = (i: number) => {
    timings.push([0, currentTime + time]);
    time += i * opts.fwUnit;
  };

  for (let i = 0; i <= morse.length; i++) {
    if (morse[i] === opts.space) {
      gap(7);
    } else if (morse[i] === opts.dot) {
      tone(1);
      silence(1);
    } else if (morse[i] === opts.dash) {
      tone(3);
      silence(1);
    } else if (
      (typeof morse[i + 1] !== 'undefined' && morse[i + 1] !== opts.space) &&
      (typeof morse[i - 1] !== 'undefined' && morse[i - 1] !== opts.space)
    ) {
      gap(3);
    }
  }

  return [timings, time];
};

export default audio;
